#preview {
    padding: 1in;
    height: 11in;
    width: 8.5in;
}
#preview * {
    font-size: 12pt;
}
#personal {
    display: flex;
    flex-direction: column;
}
#contact-info {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 5px;
}
#personal-left > * {
    margin-bottom: 6px;
}
#personal-right > * {
    margin-bottom: 6px;
}
#pName {
    font-size: 24pt;
    text-transform: uppercase;
    font-weight: bold;
}
#pTitle {
    font-size: 18pt;
}
.personal-right-line {
    display: flex;
    flex-direction: row;
    align-items: center;
}
.personal-icon {
    height: 16pt;
    width: 16pt;
    margin-right: 4pt;
}
#dividing-line {
    height: 1px;
    width: auto;
    background-color: #2D3142;
    margin-top: 10px;
    margin-bottom: 10px;
}
#pObjective {
    margin-top: 10px;
    margin-bottom: 10px;
}
#experience {
    display: flex;
    flex-direction: column;
    margin-bottom: -12px;
}
.expBlock {
    display: flex;
    flex-direction: column;
    margin-bottom: 12px;
}
.expLineOne {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 12px;
}
.expPosition, .eduProgram {
    font-weight: 500;
}
.expLineOne-right {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
#education {
    display: flex;
    flex-direction: column;
    margin-bottom: -6px;
}
.eduBlock {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 6px;
}
.eduBlock-right {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.expDates, .eduDates {
    margin-left: 4px;
    font-weight: 500;
}
#pObjective, .expDescr {
    font-weight: 300;
}
.expDescr {
    white-space: pre-wrap;
}
#experience > h2, #education > h2 {
    text-transform: uppercase;
    font-size: 16pt;
}